import React, { useState, useEffect } from 'react';
import {FaBars} from 'react-icons/fa';
import { Nav, NavbarContainer, 
NavLogo, MobileIcon, NavLinks_1,
NavMenu, NavItem, NavLinks, 
NavBtn, NavBtnLink } from './NavbarElements';
import {animateScroll as scroll} from 'react-scroll';

const Navbar = ({ toggle }) => {
    const [scrollNav, setScrollNav] = useState(false)

    const changeNav = () => {
        if(window.scrollY >= 80) {
            setScrollNav(true)
        } else {
            setScrollNav(false)
        }
    };

    useEffect(() => {
        window.addEventListener('scroll', changeNav)
    }, [])
    

    const toggleHome = () => {
        scroll.scrollToTop();
    }
// bla bla bla
  return (
    <>
        <Nav scrollNav={scrollNav}>
            <NavbarContainer>
                <NavLogo to="/" onClick={toggleHome}>CoreBuild Technologies</NavLogo>
                <MobileIcon onClick={toggle}>
                    <FaBars />
                </MobileIcon>
                <NavMenu>
                    <NavItem>
                        <NavLinks_1 to="/" onClick={toggleHome}>Home</NavLinks_1>
                    </NavItem>
                    <NavItem>
                        <NavLinks to="about"
                        smooth={true} duration={500} spy={true} exact={true} offset={-80}
                        >About</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to="services"
                        smooth={true} duration={500} spy={true} exact={true} offset={-80}
                        >Services</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to="signin">Careers</NavLinks>
                    </NavItem>
                    <NavItem>
                        <NavLinks to="footer"
                        smooth={true} duration={500} spy={true} exact={true} offset={-80}
                        >Contact</NavLinks>
                    </NavItem>
                </NavMenu>
                <NavBtn>
                    <NavBtnLink to="signin">Sign In</NavBtnLink>
                </NavBtn>
            </NavbarContainer>
        </Nav>
    </>
    );
};

export default Navbar;
